import React, {useState} from 'react';
import custom from './customSelect.css';

const CustomSelect = ({items, selectType}) => {
    const [isOpenSelect, setIsOpenSelect] = useState(false);
    const [selectedKey, setSelectedKey] = useState(0);
    const [selectedItem, setSelectedItem] = useState(items[0]);

    return <div className='custom-select-wrapper' onClick={() => setIsOpenSelect(!isOpenSelect)}>
        <div className={`custom-select ${isOpenSelect ? 'open' : ''}`}>
            <div className={'custom-select__trigger'}>
                <span>{selectedItem.name}</span>
            </div>
            <div className={'custom-options'}>
                {items.map((item, key) =>
                    <span className={`custom-option ${selectedItem === item ? 'selected' : ''}`}
                          key={key} onClick={() => setSelectedItem(item)}>
                        {`${item.name} ${item.max ? `макс. ${item.max} шт.` : ''}`}
                    </span>
                )}
            </div>
        </div>
    </div>
}

export default CustomSelect;